@import url(https://fonts.googleapis.com/css?family=Cairo);
html,
body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: #282c34;
	font-family: 'Cairo', sans-serif;
	font-weight: 400;
	color: #FAFAFA;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.header h1 {
  transition: all 0.4s ease;
	margin: 20px auto 20px auto;
	font-family: 'Cairo', cursive;
}

.switch {
  position: relative;
  display: inline-block;
  width: 48px;
  height: 28px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

.refreshCheckbox {
  display: inline-block;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 5px;
}

.slider.round:before {
  border-radius: 5px;
}

.slider-text {
  margin: 5px;  
}

.refresh-settings-container {
  position: absolute;
  right: 0;
  top:  0;
  padding: 1rem 1rem 0 0;
  margin-bottom: 1rem;
}

.refresh-interval-input {
  width: 2rem;
  text-align: center;
}
.project {
  display: block;
  font-size: 20px;
  color: black;
  font-weight: bold;
  justify-content: space-around;;
  margin: 10px 20px;
}

.title {
  color: #000;
  display: grid;
}

.row {
  display: flex;
  border: 1px solid #dddddd;
  padding: 25px;
  margin: 10px 20px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 3px 6px rgba(40, 63, 69, 0.06);
  justify-content: space-between;
}

.project a {
  text-decoration: none;
}

.project a:hover {
  text-decoration: underline;
}

.project a:link {
  color: black
}

.project a:visited {
  color: black
}

.project span.success {
  text-transform: capitalize;
}

.success .row {
  background-color: green;
}

.pending .row  {
  background-color: orange;
}

.failure .row {
  background-color:red;
}

.project button.remove{
  border: none;
  background-color: transparent;
  cursor: pointer;
  font-weight: bold;
  font-size: 20px;
  vertical-align: bottom;
}

.add-bar-container {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	width: 90vw;
	max-width: 800px;
	margin: 0 auto;
}

.add-bar {
	background-color: #efefef;
	font-size: 24px;
	font-family: 'Cairo', sans-serif;
	color: #3d3d3d;
	text-align: left;
	outline: none;
	border: 0px;
	border-radius: 5px;
	padding: 10px 20px;
	width: 100%;
}

.add-bar:focus {
	background-image: none;
}

.add-bar::-webkit-input-placeholder {
	color: #b5b5b5;
}

.add-bar:-ms-input-placeholder {
	color: #b5b5b5;
}

.add-bar::-ms-input-placeholder {
	color: #b5b5b5;
}

.add-bar::placeholder {
	color: #b5b5b5;
}

.add-bar + .add-icon {
	height: 100%;
	box-sizing: border-box;
	padding: 20px;
	position: absolute;
	top: 0;
	right: 0;
	vertical-align: middle;
	pointer-events: none;
    background-color: transparent;
	transition: opacity 0.3s;
}

.add-bar:focus + .add-icon,
.add-bar:not(:empty) + .add-icon {
	opacity: 110;
}

.error-message{
    background: tomato;
    width: 30%;
    margin-left: 35%;
    margin-top: 1%;
    padding: 0px;
    color: #fff;
    border-radius: 10px;
    opacity: 1;
    transition: opacity 0.5s ease;
}
.close {
    font-size: 14px;
    cursor: pointer;
    border-radius: 50%;
    margin-left:5%
}
.react-tabs {
    -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
    /* border-bottom: 1px solid #aaa; */
    margin: 0 0 10px;
    padding: 10px;
}

.react-tabs__tab {
    display: inline-block;
    background-color: #1180F2;
    border: 1px solid transparent;
    color: black;
    border-bottom: none;
    bottom: -1px;
    position: relative;
    list-style: none;
    padding: 6px 12px;
    cursor: pointer;
}

.react-tabs__tab--selected {
    background: #0D65CA;
    color: #fff;
    /* border-radius: 5px 5px 0 0; */
}

.react-tabs__tab--disabled {
    color: GrayText;
    cursor: default;
}

.react-tabs__tab-panel {
    display: none;
}

.react-tabs__tab-panel--selected {
    display: block;
    width: 90%;
    margin: auto;
    margin-bottom: 20px;
}
.ProjectDetails {
  position: relative;
  margin-top: 50px;
}

.ProjectDetails .autoRefreshButton {
  position: absolute;
    top: 0;
    right: 0;
    color: white;
    outline: none;
    border: none;
    background-color: black;
    padding: 10px 20px;
    font-weight: 900;
    border-radius: 4px;
}

.card {
  display: flex;
  justify-content: space-between;
  border: 1px solid #dddddd;
  padding: 25px;
  margin: 10px 20px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 3px 6px rgba(40, 63, 69, 0.06);
}

.card .name {
  width: 130px;
}

.card .message {
  width: 440px;
}
